html {
  --primary-font: "Poppins', sans-serif";
  --primary-color: #525E34;
  --primary-blue-color: #4BACAF;
  --secondary-color: #E6A481;
  --light-color: #f4fbfc;
  --error-or-danger-color: #a80000;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: transparent !important;
}

.error-message-field-generic, .error-message-field-generic p {
  color: var(--error-or-danger-color);
}

.font-16 {
  font-size: 16px !important;
}

.font-18 {
  font-size: 18px !important;
}

.font-20 {
  font-size: 20px !important;
}

.font-22 {
  font-size: 22px !important;
}

.font-35 {
  font-size: 35px !important;
}

.custom-primary-text {
  color: var(--primary-color) !important;
}

.custom-primary-blue-bg {
  background-color: var(--primary-blue-color) !important;
}

.custom-secondary-text {
  color: var(--secondary-color) !important;
}

.content-start-aligned {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.login-header {
  background-color: var(--primary-color);
  padding: 0.25rem 0;
  margin: 0;
}

.login-header .login_btn {
  background-color: var(--secondary-color);
  padding: 10px 22px !important;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 300;
}

.login-header img {
  width: 75px;
}

.login-header .middle-tabs .nav-link {
  font-size: 17px;
  font-weight: 200;
  padding: 0 1.5rem !important;
}

.login-header.navbar-light .navbar-nav .nav-link,
.login-header.navbar-light .navbar-nav .nav-link.active,
.login-header.navbar-light .navbar-nav .show>.nav-link {
  color: #fff !important;
}

.header-section {
  background-image: url(./../images/header_bg.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right;
  padding: 9rem 0 36rem 0;
  margin-top: 81px;
}

.header-section h1 {
  font-size: 4.4rem;
  color: #000000;
  text-align: left;
  margin-bottom: 2rem;
}

.header-section p {
  font-size: 1.2rem;
  color: #000000;
  text-align: left;
  width: 100%;
  min-width: 51rem;
  max-width: 51rem;
  line-height: 30px;
  margin-bottom: 2rem;
  font-weight: 500;
}

.header-section button {
  background-color: var(--secondary-color);
  color: #fff;
  padding: 1rem 2.5rem;
  border-radius: 5px;
  font-size: 18px;
  letter-spacing: 1.5px;
}

.middle-section {
  padding: 6rem 0;
}

.middle-section h2 {
  font-size: 3rem;
  text-align: center;
  color: #000000;
  margin-bottom: 5rem;
}

.middle-section .bottom-part h2 {
  margin-top: 10rem;
  margin-bottom: 7rem;
}

.middle-section .upper-part .a-side {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}

.middle-section .upper-part .a-side p {
  font-size: 1.1rem;
  color: #000000;
  width: 100%;
  min-width: 37rem;
  max-width: 37rem;
  line-height: 23px;
}

.middle-section .upper-part .b-side .card {
  background-color: #4BACAF;
  padding: 0.8rem 2rem;
  border-radius: 20px;
  margin: 0 4rem;
}

.middle-section .upper-part .b-side .header-card {
  position: relative;
  text-align: center;
}

.middle-section .upper-part .b-side .header-card .quote-icon {
  position: absolute;
  left: 0;
  top: 1rem;
  width: 79.63px;
  height: 91px;
  object-fit: cover;
}

.middle-section .upper-part .b-side .header-card h5 {
  color: #fff;
  font-size: 1.5rem;
  text-align: center;
  margin-top: 1rem;
}

.middle-section .upper-part .b-side p {
  color: #fff;
  font-size: 1.15rem;
  text-align: center;
  margin-top: 0.5rem;
  margin-bottom: 0;
}

.middle-section .bottom-part .card {
  background-color: #F9FBFF;
  padding: 0.7rem;
  border-radius: 10px;
}

.middle-section .bottom-part .card p {
  font-size: 1.65rem;
  font-weight: 500;
  color: #000000;
  margin-top: 1.2rem;
  margin-bottom: 1.5rem;
}

.middle-section .bottom-part .card button {
  text-decoration: underline;
  font-size: 1.65rem;
  color: #525E34;
  float: right;
  margin-bottom: 1.5rem;
  cursor: pointer;
}

.how-it-works-section {
  background-color: #FDFDFD;
  margin: 2rem 0;
  padding: 8rem 0;
}

.how-it-works-section h2 {
  font-size: 3rem;
  text-align: center;
  color: #000000;
  margin-bottom: 8rem;
}

.faqs-section {
  padding: 5rem 0;
}

.faqs-section h2 {
  font-size: 3rem;
  text-align: center;
  color: #000000;
  margin-bottom: 8rem;
}

.faqs-section .card {
  margin: 1rem 0rem 1rem 6rem;
  border: 0;
}

.faqs-section .card .card-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #F1F1F1;
  padding: 0;
  cursor: pointer;
}

.faqs-section .card .card-body {
  border: 1px solid #F1F1F1;
}

.faqs-section .card .card-body p {
  margin-bottom: 0;
}

.faqs-section .card .card-header h5 {
  font-size: 1.5rem;
  font-weight: 500;
  padding: 1rem 0 1rem 1.5rem;
}

.faqs-section .card .card-header .plus-icon,
.faqs-section .card .card-header .minus-icon {
  font-size: 1.5rem;
  background-color: var(--primary-color);
  color: #fff;
  width: 65px;
  height: 61px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.faqs-section .card .card-header svg {
  font-size: 1.5rem;
  color: #fff;
}

.contact-us-section {
  padding: 5rem 0;
  background-color: #FDFDFD;
}

.contact-us-section h2 {
  font-size: 3rem;
  text-align: center;
  color: #000000;
  margin-bottom: 8rem;
  font-weight: 500;
}

.contact-us-section .card {
  background-color: #E6E6E6;
  border-radius: 10px;
}

.contact-us-section .card h4 {
  font-size: 1.5rem;
  text-align: center;
  color: #000000;
  margin-bottom: 1.5rem;
  font-weight: 500;
}

.contact-us-section .card .form-control,
.contact-us-section .card textarea {
  padding: 2rem 1.5rem;
  color: #5F5F5F;
  border: 0;
  margin-bottom: 0.8rem;
  border-radius: 10px;
  font-size: 1.1rem;
  font-weight: 400;
}

.contact-us-section .card .form-control::placeholder,
.contact-us-section .card textarea::placeholder {
  color: #5F5F5F;
  font-size: 1.1rem;
}

.contact-us-section .card .form-control:focus {
  box-shadow: none;
}

.contact-us-section .card button {
  background-color: #E6A481;
  color: #fff;
  border-radius: 10px;
  width: 100%;
  padding: 1rem 1.5rem;
  font-size: 1.5rem;
  font-weight: 500;
  margin-top: 1rem;
}

footer {
  background-color: var(--primary-color);
}

.footer-section {
  padding: 2rem 0;
  border-bottom: 1px solid #ffffff40;
}

.footer-section .sama-logo-footer {
  object-fit: cover;
  width: 75px;
  margin-top: 0.5rem;
}

.footer-section a {
  font-size: 0.8rem;
  color: #fff;
  margin-top: 1.5rem;
  font-weight: 300;
}

.footer-section p {
  font-size: 0.8rem;
  color: #fff;
  margin-top: 1.5rem;
  font-weight: 300;
}

.footer-section .social-icons {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.footer-section .social-icons img {
  width: 35px;
}

.footer-section .playstore-icon {
  width: 125px;
  height: 40px;
}

.footer-bottom-section {
  padding: 1rem 0;
}

.footer-bottom-section p {
  font-size: 0.8rem;
  color: #fff;
  font-weight: 300;
  text-align: center;
  margin: 0;
}

.login-section {
  margin-top: 170px;
}

.login-section .card {
  box-shadow: 0px 1px 30px #00000029;
  border-radius: 10px;
  background-color: #FFFFFF;
}

.login-section .card-body {
  padding: 2rem 2.5rem;
}

.login-section .card img {
  width: 130px;
  margin-bottom: 2rem;
}

.login-section .card .form-control:focus+.form-label {
  top: -10px;
  left: 14px;
  z-index: 10;
  font-size: 13px;
  font-weight: 500;
  color: #E6A481;
}

.login-section .card .form-control:not(:placeholder-shown)+.form-label {
  top: -10px;
  left: 14px;
  z-index: 10;
  font-size: 13px;
  font-weight: 500;
}

.login-section .card .form-label {
  position: absolute;
  top: 12px;
  left: 25px;
  padding: 0 4px;
  font-size: 14px;
  transition: 0.5s;
  background-color: white;
  color: #ADADAD;
}

/* Style the inputs */

.login-section .card .form-control {
  border-radius: 5px;
  border: 1px solid #adadad5e;
  margin-bottom: 1.5rem;
  padding: 0.75rem 1.6rem;
}

.login-section .card .form-control:focus {
  border: 1px solid #E6A481;
  box-shadow: none;
}

/* Hide the placeholder texts (a) */

.login-section .card ::placeholder {
  color: transparent;
}

.login-section .card .inputContainer {
  position: relative;
}

.login-section .card a {
  color: #E6A481;
  font-size: 15px;
  margin-bottom: 1.5rem;
}

.login-section .card a:hover {
  text-decoration: underline;
}

.login-section .card button {
  background-color: #E6A481;
  border-radius: 5px;
  color: #fff;
  padding: 0.7rem;
  width: 100%;
  font-size: 1.2rem;
  font-weight: 400;
}

.user-menu .signOut-btn {
  height: 100%;
  display: inline-flex;
  align-items: center;
  padding: 0 15px;
}

.user-menu .signOut-btn div {
  background-color: #E6A481;
  border-radius: 10px;
  color: #fff;
  font-size: 22px;
  padding: 12px 27px;
  font-weight: 300;
}

.notification-list .notification_title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.sama-dashboard .tiles-card .card {
  background-color: var(--primary-blue-color);
  border-radius: 10px;
  color: #fff;
}

.sama-dashboard .tiles-card .card h2 {
  font-size: 2.5rem;
  text-align: right;
  margin: 0;
}

.sama-dashboard .upcoming-appointments h3 {
  font-size: 1.3rem;
  font-weight: 500;
}

.sama-dashboard .upcoming-appointments h5 {
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 0.2rem;
  margin-top: 0.3rem;
}

.sama-dashboard .upcoming-appointments p {
  margin: 0;
}

.sama-dashboard .upcoming-appointments span {
  border-radius: 5px;
  background-color: var(--primary-blue-color);
  color: #fff;
  padding: 4px 15px;
  margin-top: 1rem;
}

.sama-dashboard .upcoming-appointments img {
  width: 50px;
  margin-right: 1rem;
}

.sama-dashboard .border-bottom {
  border-bottom: 1px solid #F0F0F0 !important;
}

.sama-dashboard .border-right {
  border-right: 1px solid #F0F0F0 !important;
}

.sama-dashboard .fc .fc-button {
  background-color: var(--primary-blue-color) !important;
  color: #fff !important;
}

.sama-dashboard .fc-icon {
  color: #fff !important;
}

.doctor-tables .input-group-text {
  background-color: transparent;
  border: 1px solid #ced4da;
  border-left: 0;
  border-radius: 5px;
}

.doctor-tables .form-control {
  border-radius: 5px;
  border-right: 0;
}

.doctor-tables .form-control:focus {
  box-shadow: none;
  border: 1px solid #ced4da;
  border-right: 0;
}

.doctor-tables .card-header {
  background-color: var(--secondary-color);
  border-radius: 10px 10px 0px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 1.5rem;
}

.doctor-tables .card-header button.active,
.doctor-tables .card-header button:hover {
  background-color: #fff;
  color: var(--secondary-color);
  padding: 5px 12px;
  margin: 0 4px;
  border-radius: 4px;
}

.doctor-tables .card-header button {
  color: #fff;
  margin: 0 4px;
  padding: 5px 12px;
}

.doctor-tables .card table {
  background-color: #FCFCFC;
  padding: 1rem;
  border-radius: 0px 0px 10px 10px;
}

.doctor-tables .card table thead {
  background-color: transparent !important;
  border-bottom: 1px solid #cacaca8a;
}

.doctor-tables .card table>thead>tr>th {
  color: var(--primary-color);
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  text-transform: capitalize;
}

.doctor-tables .card table tbody tr,
.doctor-tables .card table>:not(:first-child) {
  border-bottom: none;
  border-top: none;
}

.doctor-tables .card table>:not(caption)>*>* {
  border-bottom: none;
  border-top: none;
  padding: 1rem;
}

.doctor-tables .card table img {
  width: 55px;
  padding: 2px;
  border: 1px solid var(--secondary-color);
  border-radius: 50%;
  margin-right: 10px;
}

.doctor-tables .card table .notes-added-btn {
  background-color: transparent;
  font-size: 15px;
  padding: 5px 15px;
  border-radius: 5px;
  color: var(--secondary-color);
  text-transform: capitalize;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--secondary-color);
  margin-right: 10px;
}

.doctor-tables .card table .file-btn {
  text-decoration: underline;
  cursor: pointer;
}

.doctor-tables .card table .video-call-btn {
  background-color: #000;
  font-size: 15px;
  padding: 5px 15px;
  border-radius: 5px;
  color: #fff;
  text-transform: capitalize;
  display: flex;
  align-items: center;
  border: 1px solid #000;
  justify-content: center;
}

.doctor-tables .card table .video-call-btn svg {
  color: #fff;
  margin-right: 10px;
  font-size: 20px;
}

.low_risk_btn {
  background-color: #00cd00;
  font-size: 12px;
  padding: 3px 12px;
  border-radius: 5px;
  color: #fff;
  text-transform: uppercase;
}

.medium_risk_btn {
  background-color: #ffc100;
  font-size: 12px;
  padding: 3px 12px;
  border-radius: 5px;
  color: #fff;
  text-transform: uppercase;
}