@media screen and (max-width: 767px) {
    .header-section h1 {
        font-size: 2.4rem;
        margin-bottom: 1.5rem;
    }

    .header-section {
        padding: 3rem 0 4rem 0;
    }

    .header-section p {
        min-width: 100%;
        max-width: 100%;
        line-height: 25px;
        margin-bottom: 1.5rem;
    }

    .middle-section {
        padding: 4rem 0;
    }

    .middle-section h2 {
        font-size: 2rem;
        margin-bottom: 2rem;
    }

    .middle-section .upper-part .a-side p {
        min-width: 100%;
        max-width: 100%;
    }

    .middle-section .upper-part .b-side .card {
        margin: 0 1rem;
        padding: 1rem 2rem;
    }

    .middle-section .upper-part .b-side .card .card-body {
        padding: 0rem;
    }

    .middle-section .upper-part .b-side .header-card .quote-icon {
        left: 5px;
        width: 65px;
        height: 75px;
    }

    .middle-section .upper-part .b-side .header-card h5 {
        font-size: 1.2rem;
    }

    .middle-section .upper-part .b-side p {
        font-size: 1rem;
    }

    .middle-section .bottom-part h2 {
        margin-top: 4rem;
        margin-bottom: 3rem;
    }

    .middle-section .bottom-part .card p {
        font-size: 1.25rem;
        margin-bottom: 1.25rem;
    }

    .middle-section .bottom-part .card button {
        font-size: 1.25rem;
    }

    .how-it-works-section {
        padding: 4rem 0;
    }

    .how-it-works-section h2 {
        margin-bottom: 5rem;
    }

    .timeline>li>.timeline-panel h3 {
        font-size: 1.5rem;
        margin-bottom: 2.5rem;
    }

    .timeline>li>.timeline-panel h4 {
        font-size: 1.4rem;
    }

    .faqs-section {
        padding: 3rem 0;
    }

    .faqs-section .card {
        margin: 1rem 0rem;
    }

    .faqs-section .card .card-header .plus-icon, .faqs-section .card .card-header .minus-icon {
        height: 88px;
    }

    .faqs-section .card .card-header h5 {
        font-size: 1.2rem;
    }

    .contact-us-section {
        padding: 3rem 0;
    }

    .contact-us-section h2 {
        font-size: 2rem;
        margin-bottom: 2rem;
    }

    .login-header .middle-tabs .nav-link {
        padding: 4px 1.5rem !important;
    }

    .navbar-light .navbar-toggler {
        color: #fff;
        border-color: #fff;
    }

    .navbar-toggler:focus {
        box-shadow: none;
    }

}

@media (min-width: 1200px) {
    .container {
        max-width: 1390px;
    }
}